@use './src/styles/breakpoints' as bp;
@use './src/styles/colors';
@use './src/styles/fonts';

.projects {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 30px 30px;
  grid-template-areas:
    ". . ."
    ". . .";

  div {
    border-radius: 10px;
    overflow: hidden;
    outline: none;
    user-select: none;

    a {
      height: 250px;
      width: 100%;
      display: block;
      position: relative;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
        pointer-events: none;
      }

      .highlighted_tech {
        position: absolute;
        z-index: 1;
        bottom: 10px;
        left: 10px;
        display: flex;
        gap: 5px;
        border-radius: 0;
        span {
          background-color: colors.$quaternary;
          color: colors.$primary;
          padding: 5px 10px;
          font-size: fonts.$disclaimer_small;
          line-height: 1em;
          border-radius: 4px;
          text-align: center;
        }
      }
    }
  }

  @media screen and (max-width: bp.$tabletBreakpoint) {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-template-areas:
    ". . "
    ". . ";
  }

  @media screen and (max-width: bp.$mediumMobileBreakpoint) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    div {
      max-width: 80%;
      height: 200px;
    }
  }
}

